<template>
    <div class="container" v-show="show" @click="hanleGllaryClick">
        <div class="wrapper" >
            <swiper :options="swiperOption" >
                <swiper-slide v-for="(item, index) in detailImg.imgs" :key="index">
                    <img class="gallary-img" :src="item" >
                </swiper-slide>
                <div class="swiper-pagination" slot="pagination"></div>
            </swiper>
        </div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'CommonGallary',
    data() {
        return {
            swiperOption:{
                pagination: '.swiper-pagination',
                loop:true,
                paginationType: 'fraction',
                autoplay:1500
            },
            show:true,
            detailImg:{},
            imgs:[],
            pid:this.$route.params.id
        }
    },
   methods:{ 
        getItemInfo() { 
            axios.get("static/mock/detail.json").then(res => {
                let obj = this.detailImg = res.data.data.detailImg.filter(el => el.id == this.pid)[0]
                const imgs = obj.imgs
            })
        },
        hanleGllaryClick() {
            if(window.history) {
                this.$router.go(-1) 
            }
        }
    },
    mounted() {
        this.getItemInfo()
    }
}
</script>

<style lang="stylus" scoped>
    ..container >>> .swiper-.container
        overflow:inherit;

    .container
        display:flex;
        justify-direction:column;
        justify-content:center;
        z-index:99;
        position:fixed;
        left:0;
        right:0;
        top:0;
        bottom:0;
        text-align:center;
        background:#000;

        .wrapper
            height:0;
            width:100%;
            padding-bottom:100%;
            margin:auto 0;

            .gallary-img
                width:100%;

            .swiper-pagination{
                color:#fff;
                
            }
</style>



